<template>
  <div style="margin-top: 20px; display: inline-block; width: 80vw">
    <h1 class="h3 mb-3 fw-normal">회원가입</h1>

    <div class="form-floating">
      <input
        type="email"
        class="form-control"
        id="floatingInput"
        placeholder="name@example.com"
        v-model="email"
      />
      <label for="floatingInput">이메일 주소</label>
    </div>
    <div class="form-floating">
      <input
        type="email"
        class="form-control"
        id="floatingInput"
        placeholder="name@example.com"
        v-model="username"
      />
      <label for="floatingInput">아이디</label>
    </div>
    <div class="form-floating">
      <input
        type="password"
        class="form-control"
        id="floatingPassword"
        placeholder="Password"
        v-model="password"
      />
      <label for="floatingPassword">비밀번호</label>
    </div>
    <div class="form-floating">
      <input
        type="password"
        class="form-control"
        id="floatingPassword"
        placeholder="Password"
        v-model="passwordCheck"
      />
      <label for="floatingPassword">비밀번호 확인</label>
    </div>

    <div style="margin-top: 36px">
      <span>{{ msg }}</span>
    </div>

    <button
      class="w-90 btn btn-lg mt-5"
      style="color: #fff"
      type="submit"
      @click="join"
    >
      Sign in
    </button>

    <p class="mt-5 mb-3 text-muted">&copy; khs</p>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "joinView",
  components: {},
  data() {
    return {
      username: "",
      password: "",
      passwordCheck: "",
      email: "",
      phoneNum1: "",
      phoneNum2: "",
      phoneNum3: "",
      msg: "비밀번호는 8자리 이상 그리고 대문자 1개를 포함해야합니다.",
    };
  },
  watch: {
    password() {
      if (this.password !== this.passwordCheck) {
        this.msg = "비밀번호가 일치하지 않습니다.";
      } else {
        this.msg = "";
      }
    },
    passwordCheck() {
      if (this.password !== this.passwordCheck) {
        this.msg = "비밀번호가 일치하지 않습니다.";
      } else {
        this.msg = "";
      }
    },
  },
  methods: {
    async join() {
      await axios
        .post("/api/auth/local/register", {
          username: this.username,
          password: this.password,
          email: this.email,
        })
        .then((res) => {
          localStorage.setItem("jwt", res.data.jwt);
          this.$router.push({ name: "homeView" });
          this.$store.commit("loginSuccess");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style>
.phoneNumber span {
  margin-top: 20px;
  align-self: center;
  display: flex;
  justify-content: end;
}
.phoneNumber input {
  height: 58px;
}
</style>
